@use "./styles" as *;

html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.025vw;
  line-height: 1.2;
  background: url("./image/forrest.jpg") center no-repeat;
  background-size: cover;


  @media screen and (max-device-width: $notepad){
    font-size: 1.5vw;
  }

  @media screen and (max-device-width: $tablet){
    font-size: 2.2vw;
    background: url("./image/forrest.jpg") top no-repeat;
    background-size: auto 120%;
  }

  @media screen and (max-device-width: $mobile){
    font-size: 2.7vw;
  }
}

h1,h2,h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.app {
  @include standartBlock($justify: center);
  padding: 1.5% 25%;

  @media screen and (max-device-width: $tablet){
    padding: 2.5% 15%;
  }

  @media screen and (max-device-width: $mobile){
    padding: 2.5% 5%;
  }

}
.container {
  @include standartBlock;
  background-color: $standartBackgroundColor;
  border-radius: 30px;
  color: $standartFontColor;
  gap: 1em;
  padding: 5%;
  min-height: 93vh;


  @media screen and (max-device-width: $tablet){
    min-height: 85vh;
  }
}

.header {
  @include standartBlock;

  &__title {
    font-size: 1.8em;
    text-shadow: 0px 0px 7px $fontShadowColor;
  }
}

.main {
  @include standartBlock($justify:flex-start);
  flex: 1 1 auto;
  gap: 2em;
}

.footer{
  @include standartBlock($direction: row, $justify: center);
  font-size: 0.8em;
}
